<template>
    <section>
    </section>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
    data() {
        return {
            name: ''
        }
    },
    mounted() {
        this.name = this.$route.params['name'];
        Toast({
            message: `Hello! ${this.name}`,
            iconClass: 'iconfont icon-xiaolian2',
            position: 'middle',
            duration: 5000
        });
    },
    methods: {

    }
}
</script>



